import React, { Component } from 'react';

import './style.css'
import butAdd from '../../img/add_new.png';


class ButtonAdd extends Component {

    render() {
        return ( 
            <div className='but_block'>
            	<button className={this.props.class_Name} onClick={this.props.onToggle}>
    				<img alt='' src={butAdd}/>
    			</button>
    			<p>Добавить запись</p>
            </div>			
        );
    }
}

export default ButtonAdd;